.stat{
  margin-top: 100px;

  @include mobile{
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title{
    font-family: $font-raleway;
    font-weight: 400;
    font-size: 3.6rem;
    text-align: center;

    @include mobile{
      font-size: 3rem;
    }
  }

  &__main{
    max-width: 800px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 100px auto 120px;

    @include mobile{
      flex-direction: column;
      justify-content: space-between;
      margin: 35px auto 50px;
    }
  }

  &__box{
    max-width: 160px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    font-family: $font-raleway;
    //padding-top: 100px ;
    //max-height: 16.8rem;

    @include mobile{
      margin-bottom: 28px;

      &:nth-child(3){
      margin-bottom: 0;
    }
    }
  }

  &__circle{
    width: 63px;
    height: 63px;
    border-radius: 50%;
    //z-index: -1;
    position: relative;
  }

  &__numbers{
    font-family: $font-raleway;
    font-size: 2.0rem;
    font-weight: 800;
    letter-spacing: 0.6rem;
    margin-top: 13px;
  }

  &__description{
    color: $color-gray;
    font-family: $font-raleway;
    font-size: 1.7rem;
    text-align: center;
    margin-top: 19px;
  }

}
.img{
  position: absolute;

  &__person{
    top: 0;
    right: -8px;
  }

  &__eye{
    top: 14px;
    right: -24px;
  }

  &__catalog{
    top: 6px;
    right: -18px;
  }

}

.circle{
  position: relative;

  &--red{
    background-color: $red-circle;
  }

  &--blue {
    background-color: $blue-btn;
  }


  &--green{
    background-color: $green-btn;
  }
}

